import React, { useState, useEffect } from 'react';
import axios from 'axios';

const Timetable = () => {
    const [courses, setCourses] = useState([]);

    useEffect(() => {
        fetchCourses();
    }, []);

    const fetchCourses = async () => {
        try {
            const response = await axios.get('http://localhost:8081/timetable');
            setCourses(response.data);
        } catch (error) {
            console.error('Error fetching courses for timetable:', error);
        }
    };

    // Default courses with room numbers
    const defaultCourses = [
        { _id: 1, time: '09:00 AM to 11:00 AM', name: 'Introduction to JavaScript', day: 'Monday', room: '101' },
        { _id: 2, time: '11:10 AM to 12:00 PM', name: 'React Native Development', day: 'Tuesday', room: '102' },
        { _id: 3, time: '01:45 PM to 2:40 PM', name: 'dvanced JavaScript Concepts', day: 'Wednesday', room: '103' },
        { _id: 1, time: '02:40 PM to 3:30 PM', name: 'Introduction to JavaScript', day: 'Thursday', room: '104' },
        { _id: 2, time: '3:40 PM to 4:30 PM', name: 'dvanced JavaScript Concepts', day: 'Friday', room: '105' },
        { _id: 3, time: '4:30 PM to 5:20 PM', name: 'Node.js Backend Development', day: 'Saturday', room: '106' },
    ];

    // Combining default courses with fetched courses
    const allCourses = [...defaultCourses, ...courses];

    return (
        <div>
            <h2>Timetable</h2>
            <table border="1">
                <thead>
                    <tr>
                        <th>Time</th>
                        <th>Course</th>
                        <th>Room</th>
                        
                    </tr>
                </thead>
                <tbody>
                    {allCourses.map(course => (
                        <tr key={course._id}>
                            <td>{course.time}</td>
                            <td>{course.name}</td>
                            <td>{course.room}</td>
                            
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default Timetable
